<template>
  <div class="container">
    <div class="row">
      <div class="col-md-6 kolon1">
        <div class="card">
          <div class="inner-card">
            <i class="far fa-folder" style="font-size: 5rem"></i>
            <a
              :href="link"
              style="
                text-decoration: none;
                margin-bottom: 0.8rem;
                display: block;
                color: #2e7a81;
              "
              >Download Link</a
            >
            <input
              class="email"
              type="text"
              v-model="email"
              placeholder="E-mail"
            />
            <input
              class="password"
              type="text"
              v-model="message"
              placeholder="Message"
            />

            <input
              class="button"
              type="button"
              value="Send"
              @click="sendingFolder"
            />
          </div>
        </div>
      </div>
      <div class="col-md-6 kolon2">
        <img src="../assets/image.jpg" alt="" width="600" />
      </div>
    </div>
  </div>
