<script>
import { mapGetters } from "vuex";
import emailjs from "emailjs-com";
import { init } from "emailjs-com";
export default {
  data() {
    return {
      email: "",
      message: ""
    };
  },
  methods: {
    ...mapGetters(["getDownloadLink"]),
    sendingFolder() {
      if (this.email != "" && this.message != "") {
        var template_params = {
          to_name: "John",
          reply_email: `${this.email}`,
          message: `${this.message}`,
          my_html: `<a style='display:block;text-align:center; padding-top:19px;width:180px;height:50px;border-radius:12px;color:#fff;background-color:blue;' href='${this.link}'>Download</a>`
        };
        emailjs
          .send(
            "service_gt8y0bq",
            "template_iqbiwtx",
            template_params,
            "user_RpJXvNZli3UY1aQvUCldG"
          )
          .then(() => {
            this.$router.push("/download-page");
          })
          .catch(err => {
            console.log(err);
          });
      } else {
        alert("Lütfen Boş Alan Bırakmayınız !");
      }
    }
  },

  computed: {
    link() {
      return this.getDownloadLink();
    }
  },
  mounted() {
    init("user_RpJXvNZli3UY1aQvUCldG");
  }
};
</script>

