<template>
  <div class="container">
    <div class="row">
      <div class="col-md-6 kolon1 mx-auto">
        <div class="card">
          <div class="inner-card">
            <i class="far fa-folder" style="font-size: 5rem"></i>
            <p>{{ file.filename }}</p>
            <a style="font-size:20px; display:block; color:#2E7A81; list-style-type: none; text-decoration:none;" :href="link">Download</a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  methods: {
    ...mapGetters(["getFile","getDownloadLink"])
  },
  computed: {
    file() {
      return this.getFile();
    },
    link(){
      return this.getDownloadLink();
    }
  },
  created() {
    this.file();
  }
};
</script>

