import Vue from "vue"
import Vuex from "vuex"
import FileService from "../services/FileService";
import { router } from "../router/router"
    
Vue.use(Vuex);

export const store = new Vuex.Store({
    state: {
        file: {},
        downloadLink: ""
    },
    getters: {
        getDownloadLink(state) {
            return state.downloadLink;
        },
        getFile(state) {
            return state.file;
        }
    },
    mutations: {
        setFile(state, value) {
            state.file = value;
        }
    },
    actions: {
        async addFile({ state }) {
            let formdata = new FormData();
            formdata.append("myfile", state.file)
            const response = await FileService.addFile(formdata)
            console.log(response.file)
            state.downloadLink = response.file;
            router.push("/file-send");
            return response;
        },
        async downloadFile() {
            const response = await FileService.downloadFile();
            console.log(response);
        }
    }
})