import { mapState, mapActions, mapMutations } from "vuex";
export default {
  methods: {
    ...mapState(["file"]),
    ...mapActions(["addFile"]),
    ...mapMutations(["setFile"]),
    async onChange(e) {
      this.$store.state.file = e.target.files[0];
      await this.addFile();
    
    }
  },
  computed: {
  },
  mounted() {
    this.setFile({});
  }
};
</script>

